import React, {useState, useEffect} from 'react'
import Header from './header/Header'
import Hero from './hero/Hero'
import ShoppingList from './shopping-list/ShoppingList'
import Footer from './footer/Footer'
import Cart from './cart/Cart'
import './App.scss'

function App() {

    const savedCart = localStorage.getItem('cart')

    const [cart,
        setCart] = useState(savedCart
        ? JSON.parse(savedCart)
        : [])

    const [isHomepage,
        setIsHomepage] = useState(true)

    const [isPlp,
        setIsPlp] = useState(true)

    useEffect(() => {
        isHomepage ? setIsPlp(false) : setIsPlp(true)
    }, [isHomepage])

    const deviceWidth = window.innerWidth

    useEffect(() => {
        localStorage.setItem('cart', JSON.stringify(cart))
    }, [cart])

    let cartAmount = cart.reduce((acc, cartItem) => acc + cartItem.amount, 0)
    const total = cart.reduce((acc, plantType) => acc + plantType.amount * plantType.price, 0)

    const [isOpen,
        setIsOpen] = useState(false)

    return (
        <div className="main">
            <Header
                cart={cart}
                setCart={setCart}
                setIsPlp={setIsPlp}
                cartAmount={cartAmount}
                total={total}
                isOpen={isOpen}
                setIsOpen={setIsOpen}
                isHomepage={isHomepage}
                setIsHomepage={setIsHomepage} />
            <Cart
                cart={cart}
                setCart={setCart}
                deviceWidth={deviceWidth}
                cartAmount={cartAmount}
                total={total}
                isOpen={isOpen}
                setIsOpen={setIsOpen}/> {isHomepage
                ? <Hero setIsHomepage={setIsHomepage}/>
                : <ShoppingList
                    cart={cart}
                    setCart={setCart}
                    isPlp={isPlp}
                    setIsPlp={setIsPlp}
                    deviceWidth={deviceWidth}
                    isOpen={isOpen}
                    setIsOpen={setIsOpen}/>}
            {!isHomepage && <Footer/>}

        </div>
    );
}

export default App;
